import React, { useState } from "react";
import cn from "classnames";
import OutsideClickHandler from "react-outside-click-handler";
import styles from "./TimeSlot.module.sass";
import Icon from "../Icon";
import Counter from "../Counter";

const TimeSlot = ({
  className,
  title,
  description,
  icon,
  small,
  bodyDown,
}) => {
  const [adults, setAdults] = useState(0);
  const [children, setChildren] = useState(0);
  const [babies, setBabies] = useState(0);

  const [visible, setVisible] = useState(false);

  const items = [
    {
      time: "10:00 - 11:00",
      meridiem: "am",
    },
    {
        time: "12:00 - 01:00",
        meridiem: "am",
      },
      {
        time: "01:00 - 02:00",
        meridiem: "pm",
      },
      {
        time: "0300 - 04:00",
        meridiem: "pm",
      },
      {
        time: "05:00 - 06:00",
        meridiem: "pm",
      },
      {
        time: "06:00 - 07:00",
        meridiem: "pm",
      },
      {
        time: "07:00 - 08:00",
        meridiem: "pm",
      },
  
  ];

  const renderTitle = () => {
    const sum = adults + children + babies;
    return adults > 0 || children > 0 || babies > 0
      ? sum > 1
        ? sum + " guests"
        : sum + " guest"
      : title;
  };

  return (
    <OutsideClickHandler onOutsideClick={() => setVisible(false)}>
      <div
        className={cn(
          className,
          styles.travelers,
          { [styles.small]: small },
          { [styles.bodyDown]: bodyDown },
          {
            [styles.active]: visible,
          }
        )}
      >
        <div className={styles.head}>
          <div className={styles.icon}>
            <Icon name={icon} size="24" />
          </div>
          <div className={styles.content} onClick={() => setVisible(true)}>
            {renderTitle()}
          </div>
          {description && (
            <div className={styles.description}>{description}</div>
          )}
        </div>
        <div className={styles.body}>
          <h2>Time Slots</h2>                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              
            <div className={styles.timeslot}>
          {items.map((x, index) => (
            <div className={styles.timeslotbox} key={index}>
              <div className={styles.details}>
              <p>{x.time}<span>{x.meridiem}</span></p> 
              </div>
             
            </div>
          ))}
          </div>

          <div className={styles.slotshints}>
              <span className={styles.availableslot}></span> Available time slots
          </div>
          <div className={styles.slotshints}>
          <span className={styles.unavailableslots}></span>Unavailable time slots
          </div>
          <div className={styles.slotshints} >
          <span className={styles.seletedsllots}></span> Selected time slot

          </div>
        </div>
      </div>
    </OutsideClickHandler>
  );
};

export default TimeSlot;
